var glossary=[{term:"172-point inspection",definition:"the detailed criteria every eligible pre-owned gm vehicle must meet to become certified within the gm system. every eligible vehicle that meets the basic standards must pass all 172 points, or reconditioned to meet the standards in order to become certified. this inspection is the foundation on which the certified pre-owned program is built."},{term:"average transaction price (atp)",definition:"the average price for which a vehicle is likely to sell. the certified pre-owned program helps keep the atp increased, thus protecting the brand and strengthening the residual value for new, used and cpo vehicles."},{term:"certified pre-owned (cpo)",definition:"a pre-owned vehicle that is the current model year or previous-five model years from original in-service date, has fewer than 75,000 miles, has a clean vehicle history, has no open field actions (including recalls or campaign repairs) and has undergone a rigorous inspection and reconditioning process. these vehicles offer more than $2,800 of built-in-value, a 6-year/100,000-mile powertrain limited warranty, a cpo scheduled maintenance program, a vehicle exchange program and a free vehicle history report."},{term:"certified pre-owned inventory system (cpois)",definition:"a company-wide inventory system that helps participating dealers manage their own certified-pre owned vehicles as well as gain access to other vehicles throughout the company, either from other dealers or company-owned. the intuitive system allows for complete management of your inventory and is essential for completing the certification process. it provides exclusive marketing materials, such as window stickers, and shares your inventory across many online outlets. the system is offered by the gm certified pre-owned teams supporting the chevrolet, buick, and gmc certified pre-owned programs."},{term:"dealer support center",definition:"enroll in cpois and the factory pre-owned collection by calling 888-468-7338."},{term:"end-of-term",definition:"a vehicle reaching the end of its lease contract. the customer has several options to return the vehicle, buy the vehicle or institute a short-term lease or re-lease in some cases. dealerships have the ability to purchase the vehicle to pay off the lease and add it to their inventory as well as use the vehicle as incentive to sell the buyer another vehicle."},{term:"factory pre-owned collection (fpoc)",definition:"a website that offers off-rental, off-lease and gm company vehicles simultaneously to consumers and on the wholesale market. customers can shop the site, apply for financing and have their vehicle delivered directly to a participating dealer. dealers can use the site to bid on exclusive vehicles at wholesale prices. enroll through globalconnect. the dealer support center can help you enroll."},{term:"gmcertified.com",definition:"a consumer-focused website that collects a wide array of certified pre-owned chevrolet, buick and gmc vehicles from participating dealerships throughout the nation. consumers can explore the vehicles available, purchase direct to dealer through no-hassle pricing and have the vehicle delivered directly to their local participating dealership. every cpo vehicle in your inventory is displayed on the site."},{term:"gmf dealersource",definition:"gmf dealersource is an online platform developed exclusively for gm franchise dealers that allows them to track upcoming maturities, obtain payoff quotes, ground returning vehicles and purchase pre-owned gm inventory including returned gm company car and rental vehicles, as well as gm financial off-lease inventory. gmf dealersource is available as a smartphone or tablet app for android™ and apple® users."},{term:"grounding",definition:"grounding is the process of returning a leased vehicle that is 365 days or less to contract maturity. a dealer informs gm financial that the vehicle in question has been returned and the lease contract has been ended and the vehicle rights are being transferred back to the manufacturer. dealers can easily ground vehicles using gmf dealersource, and can elect to keep the vehicle through an easy step-by-step process."},{term:"investigative vehicle history (ivh) report",definition:"a vehicle report that is accessed through globalconnect's service workbench. all certified vehicles need to have a program-compliant ivh."},{term:"maturity viewer",definition:"a feature of gmf dealersource that allows the user to track and anticipate upcoming lease maturities as well as identify early-cycle opportunities."},{term:"off-lease",definition:"a vehicle that has reached the end of its contract and is no longer under lease. until action is taken, the vehicle is considered owned by general motors."},{term:"scheduled maintenance program",definition:"cpo scheduled maintenance program helps consumers keep their vehicles in top condition with gm’s no-charge scheduled maintenance. every certified pre-owned chevrolet, buick, and gmc vehicle comes with the 2-year/24,000-mile cpo scheduled maintenance program with two included maintenance visits. services performed are oil and oil filter changes, tire rotations and multi-point inspections."},{term:"vehicle history report (vhr)",definition:"obtained through carfax® or autocheck, the vhr is a detailed report of the vehicle's history and is provided for free to customers of cpo vehicles. all cpo vehicles must pass and have a clean history report, and that report must be made available for free to potential customers and eventual buyers. the report is an essential document to be included in the final paperwork that is passed on to the customer."}];/* harmony default export */ __webpack_exports__["a"] = (glossary);

