var chaptersDefaultState = []

/*------------------------------------------------------*/

export default (state = chaptersDefaultState, action) => {
  var tempState = [...state];
  switch (action.type) {
    default:
      return tempState;
    case 'SET_CHAPTERS':
      return action.payload;

    case 'UNLOCK_PAGE':
      if(tempState[action.chapter][action.page].state.gates !== null){
        tempState[action.chapter][action.page].state.gates[action.i] = 1;
      }
      return tempState;
  }
}



